import mpvueCityPicker from '@/components/mpvue-citypicker/mpvueCityPicker.vue'
export default {
	components: {
		mpvueCityPicker
	},
	data() {
		return {
			addressData: {
				name: '',
				mobile: '',
				label: '选择省/市/区',
				address: '',
				default: false,

			},
			cityPickerValueDefault: [0, 0, 1],
			themeColor: '#d81e06',
			
		}
	},
	onLoad(option) {
		
	},
	methods: {
		switchChange(e) {
			this.addressData.default = e.detail.value?1:0;
		},

		//提交
		confirm() {
			let data = this.addressData;
			if (!data.name) {
				this.$msg('请填写收货人姓名');
				return;
			}
			if (!/(^1[0-9]{10}$)/.test(data.mobile)) {
				this.$msg('请输入正确的手机号');
				return;
			}
			if (!data.label) {
				this.$msg('请选择地区信息');
				return;
			}
			if (!data.address) {
				this.$msg('请输入详细地址');
				return;
			}
			this.$msg('保存成功')
		},
		// 三级联动选择
		showCityPicker() {
			this.$refs.mpvueCityPicker.show()
			console.log('showpicker')
		},
		onConfirm(e) {
			console.log(e)
			this.addressData.label=e.label

		},
		onCancel(){
			
		}
	},
	onBackPress() {
		if (this.$refs.mpvueCityPicker.showPicker) {
			this.$refs.mpvueCityPicker.pickerCancel();
			return true;
		}
	},
	onUnload() {
		if (this.$refs.mpvueCityPicker.showPicker) {
			this.$refs.mpvueCityPicker.pickerCancel()
		}
	}
}
